.features-Container {
  display: flex;
  max-width: 75vw;
  margin: 0 auto;
  margin-top: 201px;
  height: 431px;
}

.one {
  flex: 1;
}
.one p {
  background-image: var(--gradient-text);
  background-clip: text;
  color: transparent;
  font-family: Manrope;
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
}
.one a {
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #ff8a71;
}
.two {
  flex: 1;
}
.three {
  flex: 1;
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  /* or 171% */

  color: #81afdd;
}
.three p {
  padding: 1rem 0;
}
