.featues {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-bottom: 1rem;
}

.secondtxt {
  margin-top: 1.5rem;
  width: 334px;
  height: 92px;
  flex: 1;
}
@media only screen and (max-width: 768px) {
  .featues {
    width: 100%;
    height: max-content;
  }
  .secondtxt {
    width: fit-content;
    height: fit-content;
  }
  .theposTxt {
    width: 100%;
  }
}
